<template>
  <div class="server_div">
    <el-form ref="serverform" :model="serverform" label-width="180px">
      <el-form-item :label="$t('serverconfig.strict_check_mate')">
        <el-switch v-model="serverform.strict_check_mate" active-color="#13ce66" inactive-color="#ff4949" active-value="on"
          inactive-value="off" :active-text="$t('materialshelf.on')" :inactive-text="$t('materialshelf.off')">
        </el-switch>
      </el-form-item>
      <el-form-item :label="$t('serverconfig.access_token')">
        <el-input v-model="serverform.access_token"></el-input>
      </el-form-item>
      <el-form-item :label="$t('serverconfig.analysis_interface')">
        <el-input v-model="serverform.analysis_interface"></el-input>
      </el-form-item>
      <el-form-item :label="$t('serverconfig.mate_in_come_back_interface')">
        <el-input v-model="serverform.mate_in_come_back_interface"></el-input>
      </el-form-item>
      <el-form-item :label="$t('serverconfig.mate_out_come_back_interface')">
        <el-input v-model="serverform.mate_out_come_back_interface"></el-input>
      </el-form-item>
      <el-form-item :label="$t('serverconfig.feeder_data_interface')">
        <el-input v-model="serverform.feeder_data_interface"></el-input>
      </el-form-item>
      <el-form-item :label="$t('serverconfig.feeder_out_come_back_interface')">
        <el-input v-model="serverform.feeder_out_come_back_interface"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="serverReset">{{$t('common.reset')}}</el-button>
        <el-button type="primary" @click="serverSave">{{$t('common.save')}}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  import {
    configList,
    updateServerConfig
  } from '@/request/api/materialshelf'
  export default {
    data() {
      return {
        serverform: {
          mate_in_come_back_interface: '',
          mate_out_come_back_interface: '',
          feeder_data_interface: '',
          feeder_out_come_back_interface: '',
          analysis_interface: '',
          strict_check_mate: '',
          access_token: '',
        }
      }
    },
    mounted() {
      this.getConfig()
    },
    methods: {
      getConfig() {
        configList({}).then(res => {
          if (res.result) {
            this.serverform.mate_in_come_back_interface = res.mate_in_come_back_interface
            this.serverform.analysis_interface = res.analysis_interface
            this.serverform.mate_out_come_back_interface = res.mate_out_come_back_interface
            this.serverform.strict_check_mate = res.strict_check_mate
            this.serverform.feeder_data_interface = res.feeder_data_interface
            this.serverform.feeder_out_come_back_interface = res.feeder_out_come_back_interface
            this.serverform.access_token = res.access_token
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      },
      serverSave() {
        updateServerConfig(this.serverform).then(res => {
          if (res.result) {
            this.$message({
              showClose: true,
              message: this.$t('serverconfig.successtips'),
              duration: 3000,
              type: 'warning'
            })
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      },
      serverReset() {
        this.getConfig()
      }
    }
  }
</script>
<style>
  .server_div {
    border: 1px solid lightgray;
    margin: 40px;
    padding: 40px;
    text-align: left;
    border-radius: 8px;
  }

  .server_div .el-input__inner {
    max-width: 380px;
  }
</style>



